@import "colors.scss";
@import "layout.scss";

.mainNav {
  margin-bottom: 2rem;
  padding: 1rem 0;
  max-height: 60px;
  background: $white;
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.15);

  .navContent {
    max-width: $maxWidth;
    margin: 0 auto;
    img {
      max-height: 30px;
      object-fit: contain;
    }
  }
}
